* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.gallery {
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  justify-content: center;
  gap: 6px;
  margin: 10px;
  display: grid;
}

.photo-card {
  width: calc(24vw - 5px);
  height: calc(20vw - 5px);
  border-radius: 5px;
  margin: 5px;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  box-shadow: 3px 3px 3px rgba(184, 178, 178, .7);
}

.gallery__image {
  width: 100%;
  height: 80%;
}

.info {
  justify-content: center;
  gap: 10px;
  margin-top: 10px;
  font-size: 14px;
  display: flex;
}

.info-item {
  margin-top: 0;
}

p {
  margin: 0;
  padding: 0;
}

.search-form {
  max-width: 100%;
  background: linear-gradient(90deg, #020024 0%, #090979 35%, #00d4ff 100%);
  justify-content: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
}

input {
  width: 220px;
  height: 32px;
  border: none;
  border-radius: 5px 0 0 5px;
  outline: none;
  padding: 8px;
}

.search-btn {
  cursor: pointer;
  border: none;
  border-radius: 0 5px 5px 0;
  margin: 0;
  padding: 0 10px;
}

.search-icon {
  width: 18px;
  height: 18px;
  color: #000;
}

